'use client'

import * as React from "react"
import {
  Card,
  CardHeader,
  CardTitle,
  CardContent,
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

export default function DashboardPage() {
  const [tid, setTid] = React.useState("")
  const [data, setData] = React.useState([
    {
      merchantName: "ABC Mart",
      merchantAddress: "Dhanmondi, Dhaka",
      date: "2025-10-14",
      time: "10:23 AM",
      mid: "MID123456",
      tid: "TID987654",
      batchNo: "B001",
      stan: "ST12345",
      cardNumber: "**** **** **** 4589",
      cardType: "Visa",
      entryMode: "Chip",
      appCode: "AP1234",
    },
    {
      merchantName: "SuperShop 24",
      merchantAddress: "Banani, Dhaka",
      date: "2025-10-13",
      time: "05:11 PM",
      mid: "MID654321",
      tid: "TID123789",
      batchNo: "B002",
      stan: "ST67890",
      cardNumber: "**** **** **** 3210",
      cardType: "MasterCard",
      entryMode: "Contactless",
      appCode: "AP5678",
    },
  ])

  const handleSearch = () => {
    // For now, filter mock data (replace with API later)
    if (tid.trim() === "") {
      alert("Please enter a TID to search.")
      return
    }
    const filtered = data.filter((item) =>
      item.tid.toLowerCase().includes(tid.toLowerCase())
    )
    setData(filtered)
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle className="text-lg font-semibold">Search Transaction by TID</CardTitle>
        </CardHeader>
        <CardContent className="flex gap-3">
          <Input
            placeholder="Enter TID..."
            value={tid}
            onChange={(e) => setTid(e.target.value)}
            className="max-w-sm"
          />
          <Button onClick={handleSearch}>Search</Button>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle className="text-lg font-semibold">Transaction Details</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Merchant Name</TableHead>
                  <TableHead>Merchant Address</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Time</TableHead>
                  <TableHead>MID</TableHead>
                  <TableHead>TID</TableHead>
                  <TableHead>Batch No</TableHead>
                  <TableHead>STAN</TableHead>
                  <TableHead>Card Number</TableHead>
                  <TableHead>Card Type</TableHead>
                  <TableHead>Entry Mode</TableHead>
                  <TableHead>App Code</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {data.map((item, i) => (
                  <TableRow key={i}>
                    <TableCell>{item.merchantName}</TableCell>
                    <TableCell>{item.merchantAddress}</TableCell>
                    <TableCell>{item.date}</TableCell>
                    <TableCell>{item.time}</TableCell>
                    <TableCell>{item.mid}</TableCell>
                    <TableCell>{item.tid}</TableCell>
                    <TableCell>{item.batchNo}</TableCell>
                    <TableCell>{item.stan}</TableCell>
                    <TableCell>{item.cardNumber}</TableCell>
                    <TableCell>{item.cardType}</TableCell>
                    <TableCell>{item.entryMode}</TableCell>
                    <TableCell>{item.appCode}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
